.job-listings {
  flex: 1;
}

.job-listings-header {
  display: flex;
  align-items: center;
  margin-bottom: 16px;
}

.search-section {
  width: 100%;
}

.search-input {
  width: 100%;
  padding: 10px 14px;
  border: 1px solid #d1d5db;
  border-radius: 0;
  font-size: 14px;
  background: white;
}

.search-input:focus {
  outline: none;
  border-color: #1e3a8a;
  box-shadow: 0 0 0 3px rgba(30, 58, 138, 0.1);
}

.job-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 16px;
}

.no-jobs {
  text-align: center;
  padding: 48px 24px;
  color: #6b7280;
  font-size: 16px;
}

@media (max-width: 768px) {
  .job-grid {
    grid-template-columns: 1fr;
  }
}
